    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 99px;
    -webkit-border-radius: 99px;
    -moz-border-radius: 99px;
    background-color: #fff;
    z-index: 10; }

/* --------------------------------

Main components

-------------------------------- */
.timeline {
  position: relative;
  padding: 3em 0 3em 0;
  margin-top: 0;
  margin-bottom: 2em; }
  .timeline::before {
    /* this is the vertical line */
    content: '';
    position: absolute;
    top: 0;
    left: 18px;
    height: 100%;
    width: 2px;
    background: #fff; }

